@font-face {
  font-family: 'Ubuntu';
  src: url('/fonts/Ubuntu.eot');
  src: url('/fonts/Ubuntu.eot?#iefix') format('embedded-opentype'), url('/fonts/Ubuntu.woff2') format('woff2'), url('/fonts/Ubuntu.woff') format('woff'), url('/fonts/Ubuntu.ttf') format('truetype'), url('/fonts/Ubuntu.svg#Ubuntu') format('svg');
}

@font-face {
  font-family: 'Ubuntu-Light';
  src: url('/fonts/Ubuntu-Light.eot');
  src: url('/fonts/Ubuntu-Light.eot?#iefix') format('embedded-opentype'), url('/fonts/Ubuntu-Light.woff2') format('woff2'), url('/fonts/Ubuntu-Light.woff') format('woff'), url('/fonts/Ubuntu-Light.ttf') format('truetype'), url('/fonts/Ubuntu-Light.svg#Ubuntu-Light') format('svg');
  font-weight: 300;
}

body {
  font: 18px 'Ubuntu';
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font: inherit inherit inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.section-for-business {
  background: url('/img/for-business.png') no-repeat;
  background-size: cover;
}

.section-for-business .for-business {
  box-sizing: border-box;
  padding-top: 20px;
  min-height: 838px;
}

.section-for-business .for-business header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section-for-business .for-business header nav .for-business__nav {
  display: flex;
  font-weight: 400;
  color: #fff;
}

.section-for-business .for-business header nav .for-business__nav-item a {
  padding-right: 3rem;
  color: currentColor;
  text-decoration: none;
}

.section-for-business .for-business header nav .for-business__nav-item_tel a {
  padding-right: 0;
  color: #41FFFF;
}

.section-for-business .for-business header nav .for-business__nav-burger {
  display: none;
  position: relative;
  color: #fff;
}

.section-for-business .for-business header nav .for-business__nav-burger-icon i {
  font-size: 28px;
}

.section-for-business .for-business header nav .for-business__nav-burger-dropped {
  width: 280px;
  display: none;
  position: absolute;
  right: -5px;
  background: #fff;
  border-radius: 5px;
}

.section-for-business .for-business header nav .for-business__nav-burger-dropped-item a {
  display: block;
  padding: 10px;
  text-decoration: none;
  font-weight: 300;
  color: #333;
}

.section-for-business .for-business__text {
  width: 65%;
  padding-top: 10%;
  margin: 0 auto;
  text-align: center;
}

.section-for-business .for-business__text-head {
  font-size: 68px;
  color: #fff;
}

.section-for-business .for-business__text-small {
  padding-top: 20px;
  line-height: 1.5;
  color: #8C93B3;
}

.section-for-business .for-business__text-small a {
  color: currentColor;
}

.services {
  padding-top: 75px;
}

.services__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.services__text-head {
  font-size: 36px;
  color: #252C47;
  font-weight: bold;
}

.services__text-small {
  padding-top: 20px;
  line-height: 1.5;
  color: #302F36;
}

.services__types {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  padding-bottom: 75px;
}

.services__types-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding-top: 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.services__types-item-img {
  height: 110px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.services__types-item-head {
  color: #252C47;
  font-weight: bold;
  padding-top: 10px;
}

.services__types-item-small {
  font: 14px/1.5 'Ubuntu-Light';
  color: #302F36;
  padding-top: 15px;
}

.section-decisions {
  background: #F5F6FC;
}

.section-decisions .decisions {
  padding-top: 75px;
}

.section-decisions .decisions__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.section-decisions .decisions__text-head {
  font-size: 36px;
  color: #252C47;
  font-weight: bold;
}

.section-decisions .decisions__text-small {
  padding-top: 20px;
  line-height: 1.5;
  color: #302F36;
}

.section-decisions .decisions__tabs {
  display: flex;
  justify-content: center;
  padding-top: 20px;
}

.section-decisions .decisions__tabs-item {
  width: 197px;
  height: 40px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  border: 1px solid #3555DA;
  font-size: 14px;
  font-weight: bold;
  color: #3555DA;
  cursor: pointer;
  transition: background .2s ease;
}

.section-decisions .decisions__tabs-item:first-of-type {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
}

.section-decisions .decisions__tabs-item:last-of-type {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
}

.section-decisions .decisions__tabs-item:hover {
  background: #3555da;
  color: #fff;
}

.section-decisions .decisions__stages {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-around;
  padding: 75px 0;
}

.section-decisions .decisions__stages-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(29.166666666666668% - 20px);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 40px 20px;
  border: 1px solid #D2DAF6;
  border-radius: 7px;
}

.section-decisions .decisions__stages-item-circle {
  font: bold 36px Ubuntu;
  height: 76px;
  width: 76px;
  border: 1px solid #d2daf6;
  background: #F5F6FC;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #252C47;
  margin-top: -80px;
  margin-bottom: 25px;
}

.section-decisions .decisions__stages-item-head {
  color: #252c47;
  font-weight: bold;
}

.section-decisions .decisions__stages-item-small {
  font: 14px/1.5 'Ubuntu-Light';
  padding-top: 20px;
  color: #302F36;
}

.section-decisions .decisions__stages-item-img {
  padding-top: 28px;
  margin-top: auto;
}

.section-competences .competences {
  padding-top: 75px;
  padding-bottom: 75px;
}

.section-competences .competences__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.section-competences .competences__text-head {
  font-size: 36px;
  color: #252C47;
  font-weight: bold;
}

.section-competences .competences__text-small {
  padding-top: 20px;
  line-height: 1.5;
  color: #302F36;
}

.section-competences .competences__types {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  padding-top: 45px;
  justify-content: center;
}

.section-competences .competences__types .bcg-commerce {
  background: url('/img/commerce.svg') no-repeat right bottom;
}

.section-competences .competences__types .bcg-web {
  background: url('/img/web.svg') no-repeat right bottom;
}

.section-competences .competences__types .bcg-seo {
  background: url('/img/seo.svg') no-repeat right bottom;
}

.section-competences .competences__types-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  border-radius: 7px;
  margin-top: 20px;
  padding: 40px 25px;
  box-shadow: 0 0 15px 0 #D2DAF6;
  text-align: center;
}

.section-competences .competences__types-item-head {
  color: #252c47;
  font-weight: bold;
}

.section-competences .competences__types-item-small {
  font: 14px/1.5 'Ubuntu-Light';
  color: #302f36;
  padding-top: 15px;
}

.section-competences .competences__button {
  width: 200px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px auto 0;
  color: #fff;
  background: #3A51D2;
  font-size: 14px;
  border-radius: 7px;
  cursor: pointer;
  transition: opacity .2s ease;
}

.section-competences .competences__button:hover {
  opacity: .8;
}

.section-languages {
  background: #f5f6fc;
}

.section-languages .languages {
  padding-top: 75px;
  padding-bottom: 75px;
}

.section-languages .languages__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.section-languages .languages__text-head {
  font-size: 36px;
  color: #252C47;
  font-weight: bold;
}

.section-languages .languages__text-small {
  padding-top: 20px;
  line-height: 1.5;
  color: #302F36;
}

.section-languages .languages__types {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 45px;
}

.section-languages .languages__types-item {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
  align-items: center;
}

.section-languages .languages__types-item:nth-of-type(odd) .languages__types-item-text {
  padding-left: 40px;
}

.section-languages .languages__types-item:nth-of-type(even) .languages__types-item-img {
  order: 1;
}

.section-languages .languages__types-item:nth-of-type(even) .languages__types-item-text {
  padding-right: 40px;
}

.section-languages .languages__types-item-img {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  text-align: center;
}

.section-languages .languages__types-item-text {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
}

.section-languages .languages__types-item-text-head {
  color: #252c47;
  font-weight: bold;
}

.section-languages .languages__types-item-text-small {
  font: 14px/1.5 'Ubuntu-Light';
  color: #302f36;
  padding-top: 15px;
}

.section-portfolio {
  background: url('/img/portfolio.png') no-repeat center;
  background-size: cover;
}

.section-portfolio .portfolio {
  padding-top: 75px;
  padding-bottom: 75px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
  color: #fff;
}

.section-portfolio .portfolio__img {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(58.333333333333336% - 20px);
}

.section-portfolio .portfolio__about {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(41.66666666666667% - 20px);
}

.section-portfolio .portfolio__about-head {
  font-size: 36px;
  font-weight: bold;
}

.section-portfolio .portfolio__about-text {
  margin-top: 30px;
  font-family: 'Ubuntu-Light';
}

.section-portfolio .portfolio__about-text-head {
  font-size: 27px;
  font-weight: bold;
}

.section-portfolio .portfolio__about-text-small {
  padding-top: 10px;
  font-family: 'Ubuntu-Light';
  line-height: 1.5;
}

.section-portfolio .portfolio__about-numbers {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin-top: 30px;
}

.section-portfolio .portfolio__about-numbers-item {
  display: flex;
  flex-direction: column;
}

.section-portfolio .portfolio__about-numbers-item-numeric {
  font-size: 36px;
}

.section-portfolio .portfolio__about-numbers-item-text {
  font: 14px 'Ubuntu-Light';
}

.section-portfolio .portfolio__about-button {
  width: 200px;
  height: 40px;
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #3a51d2;
  font-size: 14px;
  border-radius: 7px;
  cursor: pointer;
  transition: opacity .2s ease;
}

.section-portfolio .portfolio__about-button:hover {
  opacity: .8;
}

.section-discussion {
  background: url('/img/discuss-gradient.png') no-repeat center;
  background-size: cover;
}

.section-discussion .discussion {
  padding-top: 75px;
  padding-bottom: 90px;
  color: #fff;
}

.section-discussion .discussion__text {
  width: 60%;
}

.section-discussion .discussion__text-head {
  font-size: 36px;
  font-weight: bold;
}

.section-discussion .discussion__text-small {
  font: 14px/1.5 'Ubuntu-Light';
  padding-top: 15px;
}

.section-discussion .discussion__contacts {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.section-discussion .discussion__contacts-item {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(20.833333333333336% - 20px);
}

.section-discussion .discussion__contacts-item-link {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

.section-discussion .discussion__contacts-item-text {
  font: 14px 'Ubuntu-Light';
  padding-top: 5px;
}

.section-discussion .discussion__contacts-form {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(41.66666666666667% - 20px);
}

.section-discussion .discussion__contacts-form-field {
  box-sizing: border-box;
  width: 227px;
  padding: 15px;
  font-size: 16px;
  border-radius: 8px;
  background: #fff;
  color: #9B9B9B;
  border: none;
  outline: none;
}

.section-discussion .discussion__contacts-form-button {
  width: 200px;
  height: 40px;
  margin-left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #3a51d2;
  font-size: 14px;
  border-radius: 7px;
  cursor: pointer;
  transition: opacity .2s ease;
  border: none;
  outline: none;
}

.section-discussion .discussion__contacts-form-button:hover {
  opacity: .8;
}

footer {
  background: #292B2E;
}

footer .footer {
  padding-top: 75px;
  padding-bottom: 75px;
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
}

footer .footer__item,
footer .footer__contacts {
  display: flex;
  flex-direction: column;
}

footer .footer__item {
  margin-left: 150px;
}

footer .footer__item-head {
  color: #fff;
  font-weight: bold;
}

footer .footer__item-small {
  text-decoration: none;
  color: #9B9B9B;
  margin-top: 20px;
