}

@media screen and (max-width: 1280px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .section-portfolio .portfolio__about-numbers {
    width: 90%;
  }

  footer .footer__item {
    margin-left: 125px;
  }
}

@media screen and (max-width: 960px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .section-for-business .for-business header nav .for-business__nav {
    display: none;
  }

  .section-for-business .for-business header nav .for-business__nav-burger {
    display: block;
  }

  .section-for-business .for-business header nav .for-business__nav-burger-dropped-item a {
    font-size: 16px;
  }

  .section-decisions .decisions__stages-item {
    width: calc(83.33333333333334% - 20px);
  }

  .section-decisions .decisions__stages-item {
    padding: 30px 10px;
  }

  .section-decisions .decisions__stages-item:not(:first-of-type) {
    margin-top: 75px;
  }

  .section-languages .languages__types-item {
    margin-top: 75px;
  }

  .section-languages .languages__types-item:nth-of-type(odd) .languages__types-item-text {
    padding-left: 0;
  }

  .section-languages .languages__types-item:nth-of-type(even) .languages__types-item-img {
    order: -1;
  }

  .section-languages .languages__types-item:nth-of-type(even) .languages__types-item-text {
    padding-right: 0;
  }

  .section-languages .languages__types-item-img {
    width: calc(66.66666666666667% - 20px);
  }

  .section-languages .languages__types-item-text {
    width: calc(66.66666666666667% - 20px);
  }

  .section-languages .languages__types-item-text {
    text-align: center;
    padding-top: 20px;
  }

  .section-portfolio .portfolio__img {
    width: calc(100% - 20px);
  }

  .section-portfolio .portfolio__img {
    display: none;
  }

  .section-portfolio .portfolio__about {
    width: calc(83.33333333333334% - 20px);
  }

  .section-portfolio .portfolio__about {
    text-align: center;
  }

  .section-portfolio .portfolio__about-numbers {
    flex-direction: column;
    width: 100%;
