  }

  .section-discussion .discussion__contacts-item {
    align-items: center;
  }

  .section-discussion .discussion__contacts-form {
    width: calc(66.66666666666667% - 20px);
  }

  .section-discussion .discussion__contacts-form {
    margin-top: 20px;
    justify-content: center;
  }

  footer .footer__item {
    margin-left: 60px;
  }
}

